:root {
    --background: #0A0A0A;
    --foreground: #FFFFFF;
    --secondary-text: #D1D1D1;
    --highlight: #00F0FF;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    height: 100%;
    scroll-behavior: smooth;
}

body {
    height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
 color: var(--foreground);
    background: var(--background);
    font-family: 'Inter', system-ui, sans-serif;
    line-height: 1.6;
}

/* Custom scrollbar for Matrix theme */
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
 background: #1A1A1A;
}

::-webkit-scrollbar-thumb {
 background: var(--highlight);
 border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: #00CC00;
}

/* Matrix rain canvas styling */
.matrix-canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 1;
}

/* Glitch text effect */
@keyframes glitch-1 {
    0%, 100% { transform: translate(0); }
    20% { transform: translate(-2px, 2px); }
    40% { transform: translate(-2px, -2px); }
    60% { transform: translate(2px, 2px); }
    80% { transform: translate(2px, -2px); }
}

@keyframes glitch-2 {
    0%, 100% { transform: translate(0); }
    20% { transform: translate(2px, -2px); }
    40% { transform: translate(2px, 2px); }
    60% { transform: translate(-2px, -2px); }
    80% { transform: translate(-2px, 2px); }
}

.glitch-text {
    position: relative;
    display: inline-block;
}

.glitch-text::before,
.glitch-text::after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.8;
}

.glitch-text::before {
    animation: glitch-1 0.3s ease-in-out infinite;
    color: #FF0000;
    z-index: -1;
}

.glitch-text::after {
    animation: glitch-2 0.3s ease-in-out infinite;
    color: #00FFFF;
    z-index: -2;
}

/* Fade in animation */
@keyframes fade-in {
    0% { opacity: 0; transform: translateY(20px); }
    100% { opacity: 1; transform: translateY(0); }
}

.animate-fade-in {
    animation: fade-in 1s ease-out forwards;
}

/* Neon glow effects */
.neon-glow {
    box-shadow: 
        0 0 5px currentColor,
        0 0 10px currentColor,
        0 0 15px currentColor,
        0 0 20px currentColor;
}

/* Matrix code styling */
.matrix-code {
    font-family: 'VT323', monospace;
    color: #00FF00;
    text-shadow: 0 0 5px #00FF00;
    white-space: nowrap;
    overflow: hidden;
}

/* Hover effects */
.hover-glow:hover {
    text-shadow: 0 0 10px currentColor, 0 0 20px currentColor, 0 0 30px currentColor;
    transform: scale(1.05);
}

/* Loading animation */
@keyframes matrix-loading {
    0% { opacity: 0; transform: translateY(20px); }
    100% { opacity: 1; transform: translateY(0); }
}

.matrix-loading {
    animation: matrix-loading 0.6s ease-out forwards;
}

/* Terminal cursor blink */
@keyframes cursor-blink {
    0%, 50% { opacity: 1; }
    51%, 100% { opacity: 0; }
}

.cursor-blink {
    animation: cursor-blink 1s infinite;
}

/* Typing animation */
@keyframes typing {
    from { width: 0; }
    to { width: 100%; }
}

.typing-animation {
    overflow: hidden;
    white-space: nowrap;
    animation: typing 2s steps(40, end);
}

/* Matrix digital rain effect */
@keyframes digital-rain {
    0% { transform: translateY(-100vh); opacity: 1; }
    100% { transform: translateY(100vh); opacity: 0; }
}

.digital-rain {
    animation: digital-rain 3s linear infinite;
}

/* Scan lines effect */
@keyframes scan-lines {
    0% { transform: translateY(-100%); }
    100% { transform: translateY(100vh); }
}

.scan-lines::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, transparent, #00FF00, transparent);
    animation: scan-lines 2s linear infinite;
}

/* Pixelated border effect */
.pixel-border {
    position: relative;
    border: 2px solid #00FF00;
}

.pixel-border::before {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: linear-gradient(45deg, #00FF00, #00CC00);
    z-index: -1;
    border-radius: inherit;
}

/* Matrix grid background */
.matrix-grid {
    background-image: 
        linear-gradient(rgba(0, 255, 0, 0.1) 1px, transparent 1px),
        linear-gradient(90deg, rgba(0, 255, 0, 0.1) 1px, transparent 1px);
    background-size: 20px 20px;
}

/* Smooth transitions */
.smooth-transition {
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Text selection styling */
::selection {
    background: rgba(0, 255, 0, 0.3);
    color: #00FF00;
}

::-moz-selection {
    background: rgba(0, 255, 0, 0.3);
    color: #00FF00;
}

/* Focus styles */
input:focus,
textarea:focus,
button:focus {
    outline: 2px solid #00FF00;
    outline-offset: 2px;
}

/* Loading spinner */
@keyframes matrix-spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.matrix-spinner {
    animation: matrix-spin 1s linear infinite;
}

/* Pill hover effects */
@keyframes pill-pulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.05); }
}

.pill-hover:hover {
    animation: pill-pulse 2s ease-in-out infinite;
}

/* Responsive utilities */
@media (max-width: 768px) {
    .glitch-text {
        font-size: 2rem;
    }
    
    .matrix-grid {
        background-size: 15px 15px;
    }
}

/* Print styles */
@media print {
    .matrix-rain,
    .glitch-text::before,
    .glitch-text::after {
        display: none;
    }
}
